import { Injectable } from "@angular/core";
import { IDisclaimerModel, IDisclaimerRequest } from "app/system-setup/org-management-setup/models/disclaimer";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class DisclaimerService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IDisclaimerRequest) {
		return this.post<IResponse<IDisclaimerModel>>("Disclaimer/Create", data);
	}

	update(data: IDisclaimerRequest) {
		return this.post<IResponse<IDisclaimerModel>>("Disclaimer/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IDisclaimerModel[]>>("Disclaimer/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IDisclaimerModel[]>>("DIsclaimer/GetAll");
	}

	Delete(Id: number) {
		return this.delete<IResponse>("Disclaimer/Delete", {
			params: { Id: Id },
		});
	}

  exportExcel() {
    this.getAll();
  }
}
