import {
  Component,
  EventEmitter,
  Inject,
  OnInit,
  Output
} from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { TreeNode } from "primeng/api";

@Component({
  selector: "app-add-edit-organization-chart",
  templateUrl: "./add-edit-organization-chart.component.html",
  styleUrls: ["./add-edit-organization-chart.component.scss"],
})
export class AddEditOrganizationChartComponent
  extends AppHelpers
  implements OnInit
{
  form: FormGroup;
  submitted = false;
  @Output() employee = new EventEmitter<any>();

  constructor(

    public dialogRef: MatDialogRef<AddEditOrganizationChartComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    super();
  }
  ngOnInit(): void {
    console.log("data", this.data.data);
    this.form = this.fb.group({
      label: [""],
      type: [""],
      data: this.fb.group({
        id: [""],
        name: [""],
        img: [null],
      }),
      children: [null],
    });
    if (this.data != null) {
      this.form.patchValue(this.data);
    }
  }
  get formCtrls() {
    return this.form.controls;
  }
  get nestedformCtrls() {
    return (<FormGroup>this.form.get("data")).controls;
  }
  submit() {
    let model: TreeNode = {};
    model.label = this.formCtrls.label.value;
    model.type = this.formCtrls.type.value;
    model.styleClass = "p-person";
    (model.expanded = true),
      (model.data = {
        id: this.nestedformCtrls.id.value,
        name: this.nestedformCtrls.name.value,
        img: this.nestedformCtrls.img.value,
      });
    model.children = this.formCtrls.children.value;
    this.dialogRef.close(model);
    this.employee.emit(model);

    console.log("data model", model);
  }

  openDeleteDialog() {
    let msg = "item";
    this.deleteDialog(msg).subscribe((confirm) => {
      // if (confirm) this.deleteNode(data);
    });
  }
}
